import React from 'react';
import styled from 'styled-components';

const Container = styled.form`
    display: flex;
    align-items: center;
    /* justify-content: center; */
    flex-direction: column;
    width: 50%;
    height: 100vh;
    margin-top: 170px;
    @media (max-width: 800px) {
        width: 100%;
        height: auto;
        margin-top: 50px;
    }
    /* background: red; */
`

const ContainerInput = styled.div`
    background: white;
    margin: 10px 0;
    padding: 10px 10px;
    width: 70%;
    max-width: 400px;
    border-radius: 20px;
    filter: drop-shadow(4px 6px 6px gray);
`

const ContainerTF = styled.div`
    background: white;
    margin: 10px 0;
    padding: 10px 10px;
    width: 70%;
    max-width: 400px;
    border-radius: 20px;
    filter: drop-shadow(4px 6px 6px gray);
`

const Input = styled.input`
    all: unset; 
    text-align: start;
    width: 100%;
    /* background: blue; */
`

const TextField = styled.textarea`
    /* all: unset; */
    border: none;
    text-align: start;
    width: 100%;
    font-size: 16px;
`


const Form = () => {
    return ( 
        <Container method="POST">                
            <ContainerInput><Input placeholder="Nombre" type="text"/></ContainerInput>
            <ContainerInput><Input placeholder="E-Mail" type="email"/></ContainerInput>
            <ContainerInput><Input placeholder="Teléfono" type="text"/></ContainerInput>
            <ContainerTF><TextField placeholder="Mensaje" cols="50" rows="15" /></ContainerTF>
        </Container>
    );
}
 
export default Form;