import React from 'react';
import Wave from './WaveComponent';
import styled from 'styled-components';
import bg from '../../img/bg.svg';
import { FaArrowCircleDown } from 'react-icons/fa'
import Fade from 'react-reveal/Fade';

const Container = styled.div`
    display: flex;
    align-items: flex-start;
    justify-content: center;
    width: 100%;
    height: 100vh;
    @media (min-width: 768px){
        height: 100vh;
        align-items: center;
    }
`

const Contenido = styled.div`
    height: 85vh;
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    @media (min-width: 1047px) {
        height: 60vh;
    }

`
const Bg = styled.img`
    width: 320px;
    position: absolute;
    right: 0;
    bottom: 19%;
    opacity: 60%;
    @media (min-width: 768px) {
        width: 50vw;
        bottom: 13%;
    }
`
const Texto = styled.p`
    font-size: 1.7rem;
    font-weight: 800;
    color: #B0DB31;
    filter: drop-shadow(3px 3px 2px rgba(0, 0, 0, 0.7)); 
    width: 60%;
    z-index: 1;
    text-align: left;
    @media (min-width: 768px) {
        font-size: 2.5rem;
        padding-right: 200px;
        text-align: center;
    }
`

const ContainerArrow = styled.div`
    width: auto;
    height: auto;
    position: absolute;
    bottom: 10%;
    z-index: 1;
`

const Link = styled.a`
    text-decoration: none;
    color: black;
`

const Info = styled.p`
    font-weight: 700;
    /* color: #0ba288; */
    color: #B0DB31;

`

const HomeComponent = () => {
    return ( 
        <Container >
            <Contenido>
                <Texto>
                    <Fade bottom>INNOVACIÓN, DEDICACIÓN Y EVOLUCIÓN, CON NOSOTROS</Fade>
                </Texto>
                    <Bg src={bg} right big />
                {/* <Fade top>
                </Fade> */}
                <ContainerArrow>
                    <Fade bottom>
                        <Info>MÁS INFORMACIÓN</Info>
                        <Link href="#services"><FaArrowCircleDown className="arrowDown" /></Link>
                    </Fade>
                </ContainerArrow>
            </Contenido>
            {/* <Wave /> */}
        </Container>
    );
}
 
export default HomeComponent;