import React from 'react';
import styled from 'styled-components';

const Container = styled.form`
    display: flex;
    align-items: flex-start;
    /* justify-content: center; */
    flex-direction: column;
    width: 50%;
    height: 100vh;
    margin-top: 150px;
    @media (max-width: 800px) {
        width: 100%;
        height: auto;
    }
    /* background: red; */
`

const Titulo = styled.h1`
    padding-left: 40px;
    color: #B0DB31;
`

const Info = styled.p`
    padding-left: 40px;
    font-size: 16px;
    font-weight: 500;
    font-style: italic;
    color: black;
    text-align: left;
    color: #424242;
`

const Negrita = styled.b`
    font-weight: 800;
    font-style: initial;
    
`

const Mail = styled.a`
    text-decoration: none;
    color: black;
    &:hover {
        color: #B0DB31;
    }
`

const ContainerMapa = styled.div`
    width: 100%;
    margin-top: 30px;
    border-radius: 20px;
    overflow: hidden;
    filter: drop-shadow(2px 2px 2px gray); 
`


const Datos = () => {
    return ( 
        <Container>
            <Titulo>Contáctanos</Titulo>
            <Info><Negrita>Dirección:</Negrita> Moreno 970 Piso 5, CABA, Argentina</Info>
            <Info><Negrita>Teléfono:</Negrita> +54 11-5353-5515</Info>
            <Info><Negrita>E-Mail:</Negrita> <Mail href="mailto: hola@geco.com.ar">hola@geco.com.ar</Mail></Info>
            <ContainerMapa><iframe width="90%" height="400" frameborder="0" scrolling="no" marginheight="0" marginwidth="0" src="https://maps.google.com/maps?width=100%25&amp;height=400&amp;hl=es&amp;q=Moreno%20970%20Piso%205,%20Ciudad%20Aut%C3%B3noma%20de%20Buenos%20Aires,%20Argentina+(geco%20%20%7C%20Technology%20&amp;%20Services)&amp;t=&amp;z=14&amp;ie=UTF8&amp;iwloc=B&amp;output=embed"></iframe></ContainerMapa>
        </Container>
    );
}
 
export default Datos;